<template>
  <div class="back">
    <v-row class="d-flex justify-end back2">
      <v-col cols="12" sm="4">
        <v-select
          :items="schoolList"
          label="选择高校"
          v-model="selectSchool"
          outlined
        ></v-select>
      </v-col>

      <v-col cols="12" sm="4">
        <v-select
          :items="yearList"
          label="选择年份"
          v-model="selectYear"
          outlined
        ></v-select>
      </v-col>
      <v-col cols="12" sm="2" class="mt-3">
        <v-btn color="light-blue accent-3 white--text" @click="searchSci()">
          <v-icon>mdi-magnify</v-icon>查询
        </v-btn>
        <v-btn color="amber darken-3" @click="reset()" class="ml-3">
          <v-icon>mdi-cached</v-icon>重置
        </v-btn>
      </v-col>
    </v-row>
    <div class="d-flex space-between in justify back">
      <v-col cols="2">
        <v-card
          outlined
          class="d-flex flex-row mr-5 mb-12"
          max-height="750"
          max-width="152"
        >
          <v-list>
            <v-list-item-group v-model="selectSubject" color="indigo">
              <v-list-item
                v-for="item in menuLists"
                :key="item.id"
                @click="handleToList(item.catename, item.id)"
              >
                <v-list-item-icon v-if="hasIcon">
                  <v-icon>mdi-format-list-checkbox</v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title v-text="item.catename"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card>
      </v-col>
      <v-col cols="22">
        <v-card class="flex-row-reverse ml-5 mb-12 pt-2" outlined elevation="0">
          <div class="list-right flex-row ml-5 mt-3">
            <h4>{{ catename }}</h4>
            <span class="float-right pr-5 grey--text"
              >您的位置: 首页 > 科研成果</span
            >
          </div>
          <!--命名路由-->
          <router-view name="list"></router-view>
          <router-view name="detail"></router-view>
        </v-card>
      </v-col>
    </div>
  </div>
</template>

<script>
export default {
  inject: ['reload'],
  name: 'Research',
  data() {
    return {
      catename: '船舶产业',
      hasIcon: true,
      selectSchool: '',
      selectYear: '',
      menuLists: [
        { id: 0, catename: '船舶产业' },
        { id: 1, catename: '金属材料' },
        { id: 2, catename: '能源石化' },
        { id: 3, catename: '汽车配件' },
        { id: 4, catename: '电机电器' },
        { id: 5, catename: '通（专）用设备' },
        { id: 6, catename: '工程机械' },
        { id: 7, catename: '高端装备' },
        { id: 8, catename: '新材料' },
        { id: 9, catename: '生物医药' },
        { id: 10, catename: '电子信息' },
        { id: 11, catename: '节能环保' },
        { id: 12, catename: '其他' }
      ],
      schoolList: [],
      yearList: [
        { text: '2005' },
        { text: '2006' },
        { text: '2007' },
        { text: '2008' },
        { text: '2009' },
        { text: '2010' },
        { text: '2011' },
        { text: '2012' },
        { text: '2013' },
        { text: '2014' },
        { text: '2015' },
        { text: '2016' },
        { text: '2017' },
        { text: '2018' },
        { text: '2019' },
        { text: '2020' }
      ],
      selectSubject: 0
    }
  },
  methods: {
    hasNoIcon() {
      if (document.body.clientWidth < 600) {
        this.hasIcon = false
      }
    },
    handleToList(catename, id) {
      this.catename = catename
      this.$store.commit('handleSubject', catename)
      this.$store.commit('handleSelectSubject', id)
      this.$router.push({
        path: '/company/research/list/' + this.catename,
        query: {
          school: this.selectSchool,
          year: this.selectYear
        }
      })
    },
    reset() {
      this.selectSchool = ''
      this.selectYear = ''
      this.$router.push(this.catename)
      this.reload()
    },
    async getList() {
      let res = await this.$http.get('/api/home/alluniversity')
      let sList = res.data.data.users
      for (let i = 0; i < sList.length; i++) {
        this.schoolList.push(sList[i].username)
      }
    },
    async searchSci() {
      this.$router.push({
        path: this.catename,
        query: {
          school: this.selectSchool,
          year: this.selectYear
        }
      })
    }
  },
  mounted() {
    this.hasNoIcon()
    this.getList()
    let catename = sessionStorage.getItem('subject')
    let selectSubject = sessionStorage.getItem('selectSubject')
    if (catename == null && selectSubject == null) {
      catename = '船舶产业'
      selectSubject = 0
    }
    this.catename = catename
    this.selectSubject = selectSubject
    this.handleToList(this.catename, this.selectSubject)
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
.back2 {
  background-color: #fff;
}
.list-right h4 {
  float: left;
  height: 35px;
  line-height: 25px;
  font-size: 22px;
  color: #003366;
  border-bottom: 2px solid #003366;
  font-weight: bold;
}
.list-right {
  border-bottom: 1px solid #ccc;
  height: 35px;
}
</style>
